@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
:root {
    --white: #F8F8FF;
    --gray: #808080;
    --darker-gray: #adaaaa;
    --mustard: #fcba03;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #F8F8FF;
    background-color: var(--white);
    font-family: 'Montserrat', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 1vh;
    margin: auto;
    color: #F8F8FF;
    color: var(--white);
    background-color: #808080;
    background-color: var(--gray);
    text-align: center;
}
header {
    position: fixed;
    top: 0;
    width: 100%;
    height: 8vh;
    background-color: var(--gray);
    z-index: 10;
}

h1 {
    color: var(--white);
    padding: 1vh;
}
.author-list {
    background-color: var(--mustard);
    grid-template-rows: 3em 1fr !important;
    justify-items: center;
    align-items: center;
    grid-gap: 0 !important;
    cursor: pointer;
}

li:hover .author-list {
    text-decoration: underline;
    cursor: pointer;
}
.author {
    text-align: center;
    padding-top: 0.5em;
}

#albums-list {
    top: 0vh;
}

li img:hover {
    cursor: pointer;
}

ul {
    margin-bottom: 3vh;
}
.album-container {
    position: absolute;
    top: 8vh;
    width: 100%;
    height: 100%;
    background-color: var(--white);
}

#album {
    top: 0vh;
}

#btn-back {
    position: fixed;
    right: 0;
    border-width: 0;
    font-family: "Montserrat", sans-serif;
    color: var(--gray);
    font-weight: bold;
    font-size: 13px;
    padding: 0.5em;
    cursor: pointer;
}

#btn-back:hover {
    background-color: var(--darker-gray);
    border-width: 0;
}

.album {
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: 90%;
    grid-gap: 1em;
    padding: 2em 7em;
    background-color: var(--white);
}

.album .album-picture {
    display: grid;
    grid-template-rows: 15em 1fr;
    grid-gap: 1em;
}

figure > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media all and (min-width: 576px) {
    .album {
        grid-auto-flow: initial;
        grid-template-columns: repeat(auto-fit, minmax(auto, 16em));
        justify-content: center;
    }
}

h4 {
    text-align: center;
    padding-top: 1em;
}

ul {
    list-style: none;
}

figure {
    margin: 0;
}

figcaption {
    line-height: 1.4;
    text-align: center;
    color: var(--gray);
}

.slideshow-container {
    max-width: 1000px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.current-slide {
    display: block;
    margin: auto;
}

.close {
    position: absolute;
    top: 0.2em;
    right: 0.2em;
    padding: 0 0.2em;
    color: var(--white);
    font-size: larger;
    font-weight: bold;
    background-color: transparent;
    border-width: 0;
}

.close:hover, .close:active, .close:focus {
    color: var(--darker-gray);
    cursor: pointer;
}

.prev, .next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    padding: 0 1em;
    color: var(--white);
    font-weight: bold;
    font-size: larger;
    transition: 0.3s ease;
    border-width: 0;
    background-color: transparent;
}

.next {
    right: 0;
}

.prev:hover, .next:hover {
    color: var(--gray);
}

.prev:focus, .next:focus {
    outline: none;
}

.number {
    color: var(--white);
    font-size: 0.8em;
    font-weight: bolder;
    padding: 0.8em;
    position: absolute;
    top: 0;
}

figcaption {
    color: var(--gray);
    font-size: 12px;
    background-color: var(--white);
    padding: 0.5em;
}


